import getComponentRootDom from './getComponentRootDom';
import Icon from '@/components/Icon';
import style from './showMessage.module.less';
/**
 * 
 * @param {String} content 
 * @param {String} type  info  error  success  warn 
 * @param {Number} duration  
 * @param {HTNLElement} container 
 */
export default function( options ) {
    const content = options.content || '';
    const type = options.type || 'info';
    const duration = options.duration || 2000;
    const container = options.container || document.body;
    const div = document.createElement('div');
    const iconDom = getComponentRootDom(Icon, { type });  // icon dom
    div.innerHTML = `<span class="${style.icon}">${iconDom.outerHTML}</span><div>${content}</div>`;
    
        //  position 
        if( options.container ) {
            if( getComputedStyle(container).position === 'static') {
                container.style.position = 'relative';
            }
        }

    div.className = `${style.message} ${style[`message-` + type]}`;
    container.appendChild(div);

    //
    div.clientHeight;
    
    // 
    div.style.opacity = 1;
    div.style.transform = `translate(-50%, -50%)`;

    setTimeout(() => {
        div.style.opacity = 0;
        div.style.transform = `translate(-50%, -50%) translateY(-25px)`;
        addEventListener('transitionend',function() {
            div.remove();
            //
            options.callback && options.callback();
        }, {once: true})
    }, duration);
    
}